// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";

// Custom styles
body {
	overflow: hidden;
}
button {
	outline: none;
}
.index-wrapper {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 100vh;
	position: relative;
	overflow: hidden;
}
.overlay {
	position: fixed;
	display: none;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0,0,0,0.5);
	z-index: 10;
	cursor: pointer;
	&.show {
		display: block;
	}
}
.topnav {
	background-color: #fff;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 10px 30px 10px 30px;
	z-index: 10;
	@media(max-width: 456px) {
		padding: 10px 20px;
	}
	.dropdown {
		position: relative;
		//&:before {
		//	content: '';
		//	position: absolute;
		//	top: -26px;
		//	left: -65px;
		//	height: 81px;
		//	width: 1px;
		//	background-color: rgba(221, 221, 221, 0.3);
		//}
		.dropbtn {
			cursor: pointer;

			font-size: 18px;
			font-weight: 400;
			line-height: 27.52px;
			text-transform: uppercase;
			letter-spacing: 0.72px;
			color: #205880;
			background-color: transparent;
			border: none;
			position: relative;
			&:after {
				content: '';
				position: absolute;
				top: 10px;
				right: -15px;
				width: 13px;
				height: 9px;
				background: url("../images/sprites/png/dropdown.png") center no-repeat;
				@media (max-width: 785px) {
					top: 23px;

 				}
			}

		}
		@media (max-width: 1440px){
			padding-right: 15px;
		}
		&:first-of-type {
			@media (max-width: 1440px){
				display: none;
			}

		}

		@media (max-width: 518px){
			display: none;
		}
		//
		//&:nth-child(3) {
		//	@media (max-width: 1149px) {
		//		display: none;
		//	}
		//}
		//@media (max-width: 1149px) {
		//	display: none;
		//}

		.dropdown-content {
			display: none;
			position: absolute;
			background-color: #f9f9f9;
			max-width: 300px;
			width: 100%;
			box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
			z-index: 1;
			a {
				background-color: #ebebeb;
				color: #205880;
				padding: 12px 16px;
				text-decoration: none;
				display: block;
				text-align: left;

				&:hover {
					transition: .3s ease;
					background-color: #f9f9f9;
				}
			}
		}
		&:hover {
			.dropdown-content {
				display: block;
			}
		}
	}


	&__contacts {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		padding-left: 37px;
		@media (max-width: 1048px) {
			display: none;
		}

		@media (max-width: 518px){
			display: flex;
		}
		.phone {
			position: relative;
			color: #505254;
			font-size: 18px;
			font-weight: 400;
			line-height: 25px;
			text-decoration: none;
			margin-bottom: 11px;
			&:after {
				position: absolute;
				content: '';
			    top: 3px;
   			    left: -32px;
				width: 24px;
				height: 24px;
				background: url('../images/sprites/png/phone.png') center no-repeat;

			}
		}

		.email {
			color: #2f9ba8;
			font-size: 16px;
			font-weight: 400;
			line-height: 25px;
			text-decoration: underline;
			position: relative;
			&:after {
				position: absolute;
				content: '';
			    top: 3px;
   			    left: -32px;
				width: 24px;
				height: 24px;
				background: url('../images/sprites/png/mail.png') center no-repeat;

			}
		}
	}
	.icon {
		font-size: 24px;
		color: #205880;
		cursor: pointer;
	}
}

.sidebar {
 	height: 100%;
	width: 290px;
	background-color: #f9f9f9;
	position: fixed;
	top: 0;
	padding-top: 89px;
	box-shadow: 8px 0 5px -2px rgba(124, 124, 124, 0.43);
	z-index: 11;
	overflow: auto;
	.dropdown {
		margin-bottom: 30px;
		.dropbtn {
			padding: 6px 8px 6px 16px;
			text-decoration: none;
			font-size: 16px;
			color: #205880;
			text-transform: uppercase;
			display: block;
			border: none;
			background: none;
			width: 100%;
			text-align: left;
			cursor: pointer;
			outline: none;
			position: relative;
			&:after {
				content: '';
				position: absolute;
				top: 10px;
				right: 19px;
				width: 13px;
				height: 9px;
				background: url("../images/sprites/png/dropdown.png") center no-repeat;
			}
		}

		.dropdown-content {
			display: none;
			max-width: 300px;
			width: 100%;
			box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
			z-index: 1;
 			a {
				background-color: #ebebeb;

				color: #205880;
				padding: 12px 16px;
				text-decoration: none;
				display: block;
				text-align: left;
				&:hover {
					transition: .3s ease;
					background-color: #f9f9f9;
				}
			}

			.topnav__contacts {
				background-color: #ebebeb;
				.phone{


					&:after {
						top: 14px;
						left: -20px;
					}
				}
				.email{


					&:after {
						top: 14px;
						left: -20px;
					}
				}
			}
		}
		&.active {
			.dropdown-content {
				display: block;
				background-color: #fafafa;
			}
			.dropbtn {
				background-color:#f9f9f9;
			}
		}
	}
	&__close {
		font-size: 33px;
		float: right;
		background-color: transparent;
		border: none;
		color: #1c5781;
		margin-right: 10px;
		margin-bottom: 30px;
		cursor: pointer;
	}
	&.closed {
		display: none;
	}
	&.open {
		display: block;
	}
}
.video {
	position: fixed;
	right: 0;
	bottom: 0;
	z-index: -1;
	min-width: 100%;
	min-height: 100%;
	background-color: rgba(70, 70, 70, 0.3);
}

.main {
	&__content {
		position: fixed;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		max-width: 452px;
		text-align: center;
	}
	&__title {
		color: #ffffff;
		font-weight: 100;
		font-size: 70px;
		line-height: 80px;
		font-family: "Lato Thin";
		@media (max-width: 471px) {
			font-size: 50px;
		}

		span {
			font-family: "Mint Spirit";
			font-size: 70px;
			font-weight: bold;
			line-height: 80px;
			margin-left: 15px;
			@media (max-width: 471px) {
				font-size: 50px;
			}
		}
	}

	&__bottom-menu {
		align-self: center;
		flex-wrap: wrap;
		display: flex;
		flex-direction: row;
		justify-content: center;
		@media(max-width: 768px) {
			display: none;
		}
 		&-link {
			color: #ffffff;
			font-size: 16px;
			font-weight: 500;
			line-height: 70px;
			text-transform: uppercase;
			text-decoration: none;
			margin-right: 44px;
			@media (max-width: 905px) {
				margin-right: 20px;
			}
			&:last-of-type {
				margin-right: 0;
			}
			&.logo {
				color: #ffffff;
				font-family: "Mint Spirit";
				font-size: 24px;
				font-weight: 400;
				line-height: 68.21px;
				text-transform: none;
				margin-right: 63px;
				@media (max-width: 500px) {
					width: 100%;
					text-align: center;
					line-height: 1;
					margin-bottom: 25px;
					margin-right: 0;
				}
				@media (max-width: 800px) {
					margin-right: 30px;
				}


			}
			@media (max-width: 500px) {
				width: 100%;
				text-align: center;
				line-height: 1;
				margin-bottom: 15px;
				margin-right: 0;
			}
		}
 	}
}
